export default {
	data() {
		return {
			datas: [],
			inputValue: "", //搜索输入框内容
			page: 1, //默认请求第一页
			pageSize: 10, //默认请求十条数据
			url: ''
		}
	},

	methods: {
		// 选中公司
		chooseCompany(e) {
			const newAddress = this.datas[e].addr
			uni.setStorage({
				key: 'company',
				data: newAddress
			})
			uni.navigateTo({
				url: this.url //+'?newaddress='+newAddress
			})
		},
		//输入框搜索
		searchCompany() {
			if (this.inputValue !== '' && this.inputValue !== null) {
				this.test.post('base/listCustCompany', {
					search: this.inputValue,
					page: this.page,
					pageSize: this.pageSize,
				}).then(res => {
					console.log(res)
					if (res.statusCode == 200 && res.data.errorCode == 0) {
						// console.log(res.data.value.length)
						this.datas = res.data.value;
						if (res.data.value.length === 0) {
							uni.showToast({
								title: '没有更多了',
								icon: 'none'
							})
						}
					} else {

					}
				}).catch(err => {
					console.log(err)
				})
			} else if (this.inputValue == '' && this.inputValue == null) {

				this.getCompanyInf()
			}
		},
		getCompanyInfo() {
			console.log(this.page,this.pageSize)
			this.test.post('base/listCustCompany', {
				search: '',
				page: this.page,
				pageSize: this.pageSize
			}).then(res => {
				console.log(res)
				if (res.statusCode == 200 && res.data.errorCode == 0) {
					this.datas = res.data.value
				}
			}).catch(err => {
				console.log(err)
			})
		},
		more(){
			this.page += 1;
			this.getCompanyInfo()
		}
	},
	onShow() {
		this.getCompanyInfo()
	},
	onLoad(option) {
		this.url = option.address
	}
}
